<template>
    <div class="category">
        <van-nav-bar :title="typeName" left-arrow @click-left="back()" class="van-head">
        </van-nav-bar>

        <div class="category-tag">
            <div class="category-tag-box  clearfix">
                <div class="category-tag-title fl" :class="item.isActive ? 'active' : ''"  v-for="(item,index) in categoryType" :key="index" @click="onChange(item,index)">{{item.title}}</div>
            </div>
        </div>

        <div class="category-list-box">
            <div class="category-list-book clearfix" v-for="(item,index) in categoryBookData" :key="index" @click="skipId(item._id)">
                <div class="category-list-img fl">
                    <img class="auto-img" :src="item.cover" alt="">
                </div>
                <div class="category-list-item fr">
                    <div class="category-list-title">{{item.title}}</div>
                    <div class="category-item-bookmask clearfix">
                        <div class="fl">{{item.author}}</div>
                        <div class="fl category-item-center">|</div>
                        <div class="fl">{{item.majorCate}}</div>
                    </div>
                    <div class="category-list-content">
                        {{item.shortIntro}}
                    </div>
                    <div class="clearfix category-item-bookmask">
                        <div class="fl clearfix">
                            <div class="fl category-item-moods">{{item.latelyFollower}}万</div>
                            <div class="fl">人气</div>
                        </div>
                        <div class="fl category-item-center">|</div>
                        <div class="fl">
                            <div class="fl category-item-moods">{{item.retentionRatio}}%</div>
                            <div class="fl">读者留存</div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="category-list-hr"></div>
        </div>
    </div>
</template>

<script>
import '../assets/css/category.less'
export default {
    data(){
        return{
            categoryType:[
                {
                    title: '热门',
                    activeName:'hot',
                    isActive: true
                },
                {
                    title:'新书',
                    activeName:'new',
                    isActive:false
                }
                ],
            typeName:'',
            categoryBookData:{},
            typeActive:'',
            hot:'hot'
        }
    },
    created(){
        //获取数据
        this.getcategoryBookData();
        this.typeName = this.$route.query.type;
    },
    methods:{
        //点击切换
        onChange(item,index){
            if(item.isActive){
                return;
            }
            for(let i=0;i<this.categoryType.length;i++){
                if(this.categoryType[i].isActive){
                    this.categoryType[i].isActive = false;
                    break;
                }
            }
            this.categoryType[index].isActive = true;
            this.hot = this.categoryType[index].activeName;
            this.getcategoryBookData();
        },
        //返回
        back(){
            this.$router.go(-1);
        },
        // id跳转
        skipId(id){
             
            this.$router.push({name:'Detail',query:{
                id:id
            }})
        },
        getcategoryBookData(){
            this.$toast.loading({
                message:'加载中...',
                forbidClick: true,
                duration: 0
            });

            this.axios({
                method:'GET',
                url:'http://novel.kele8.cn/category-info?gender='
                +this.$route.query.gender
                +'&type='+this.hot
                +'&major='
                +this.$route.query.type
                +'&minor=&start=0&limit=200'
            }).then(result => {
                this.$toast.clear();
                
                if(result.data.books){
                    this.categoryBookData = result.data.books;
                    //  

                }
                //遍历数组
                this.categoryBookData.forEach((val) => {
                    val.cover = unescape(val.cover).slice(7);
 
                })
                this.categoryBookData.forEach((val) => {
                    val.latelyFollower = (val.latelyFollower/10000).toFixed(1);
                })
            }).catch(err => {
                this.$toast.clear();
                 
            })
        },

    }
}
</script>